        created(){
            this.getEconomicsData();
            this.getStockData();
        },
        methods:{

            getEconomicsData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1348648756099',
                        page:pageNum,
                    }
                }).then((result) =>{
                    console.log('result',result)
                    this.EconomicsData = result.data.data
                }).catch((err) =>{
                    console.log('err',err)
                })
            },


            getStockData(pageNums){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1473054348939',
                        page:pageNums,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.StockData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },

             //点击页码，获取对应的val值，在传给要显示的页数内容
