                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getEconomicsData(pageNum);
            
            },
              handleCurrentChangeStock(val){
                
                // console.log('val',val)
                let pageNums = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getStockData(pageNums)
            
            },
